@import "styles/colors.scss";

$spacing-1x: 20px;
$spacing-2x: $spacing-1x * 2;

:root {
  --main-bg-color: #030c5e;
  --second-bg-color: #050d50;
  --gradient-color-1: #ce09ff;
  --gradient-color-2: #ffa34e;
  --gradient-color-3: #ce09ff;
  --gradient-color-4: #48f573;
  --main-black: #010845;
  --second-black: #030b4d;
  --light-bg: #2f1385;
  --blur: #ff00f5;
}

hr {
  margin: 50px 20px;
  border-color: rgb(255, 255, 255, 0.2);
}

h1 {
  font-size: 24px;
  font-weight: 500;
  margin-bottom: $spacing-1x;

  text-align: left;
}

p {
  text-align: left;
}

.spacing-top-1x {
  margin-top: $spacing-1x;
}

.spacing-top-1dot5x {
  margin-top: 15px;
}

.spacing-top-2x {
  margin-top: $spacing-2x;
}

.spacing-bottom-1x {
  margin-bottom: $spacing-1x;
}

.spacing-bottom-1dot5x {
  margin-bottom: 15px;
}

.spacing-bottom-2x {
  margin-bottom: $spacing-2x;
}

button {
  border-radius: 8px;
  padding: 20px 0;
  align-self: stretch;
  color: white;
  outline: 0;
  border: 0;

  &:hover {
    cursor: pointer;
  }

  &:disabled {
    background-color: #2e2380;
    color: #5d6074;
  }

  &:not(:disabled) {
    background: linear-gradient(270.9deg, var(--gradient-color-1) -2.92%, var(--gradient-color-2) 99.95%);
  }
}

.page {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.App {
  padding: 20px;
  height: 100%;

  text-align: center;
  background-color: #170b71;
  display: flex;
  flex-direction: column;

  color: white;
  font-family: "Farro", sans-serif;

  .main-title {
    color: $pinkDark;
    font-size: 22px;
    padding: 20px 0;
  }

  .info-title {
    color: white;
    margin-bottom: 16px;
  }

  .page-content {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    flex: 1;
  }

  .action-buttons {
    flex-grow: 1;
    width: calc(100% - 30px);
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    align-items: center;

    &__button {
      margin: 0 0 10px;
      width: 100%;
      max-width: 400px;
      height: 50px;
      color: white;
      cursor: pointer;

      display: flex;
      align-items: center;
      justify-content: center;
      border: 2px solid $pinkGlow;
      background-color: transparent;

      &-dark {
        background-color: $pinkDark;
      }

      &,
      &:active,
      &:hover {
        outline: none;
        text-decoration: none;
      }
    }
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
