.actions {
    margin-top: 15px;

    display: flex;
    justify-content: space-around;

    > button {
        width: 200px;
    }
}

.error {
    color: red;
}

.is-transacting {
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    top: 0;
    left: 0;

    .content {
        position: absolute;
        top: calc(50% - 50px);
        left: calc(50% - 150px);
        width: 300px;
    }
}
