#choose-allocation-page {
    h2 {
        text-align: left;
        margin-bottom: 10px;
    }

    .input-header,
    .input-footer {
        text-align: left;
        font-size: 14px;
    }
    .input-header {
        margin-bottom: 10px;
    }

    .input-footer {
        margin-top: 10px;

        display: flex;
        justify-content: space-between;
    }

    .previewToken {
        padding: 5px;

        display: flex;
        align-items: center;

        background-color: #2e2380;

        border-radius: 8px;

        > img {
            width: 40px;
        }

        > .symbol {
            width: 80px;
        }

        > .info {
            width: 80px;

            text-align: left;

            color: #8983b9;
        }

        > .amount {
            padding-left: 15px;

            flex: 1;

            border-left: 1px solid #8983b9;

            text-align: left;
        }

        & + .previewToken {
            margin-top: 10px;
        }
    }

    .error {
        color: red;
    }
}
