.collapsible {
    display: flex;
    flex-direction: column;

    padding: 20px;

    margin-bottom: 10px;

    border-radius: 8px;

    background-color: #281981;

    &.isOpen {
        background: linear-gradient(270.9deg, var(--gradient-color-1) -2.92%, var(--gradient-color-2) 99.95%);
    }

    > .header {
        display: flex;
        align-items: center;
        justify-content: space-between;

        cursor: pointer;
    }

    > .body {
        border-top: 1px solid rgba(255, 255, 255, 0.2);
        padding-top: 10px;
        margin-top: 10px;
    }
}

/**
 *      CUSTOM THEME
 */
.collapsible {
    > .body {
        text-align: left;
    }
}
