.stepper-wrapper {
    display: flex;
    justify-content: space-between;

    > .item {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        flex: 1;

        @media (max-width: 768px) {
            font-size: 12px;
        }

        &::before {
            position: absolute;
            content: "";
            border-bottom: 2px solid #281981;
            width: 100%;
            top: 20px;
            left: -50%;
            z-index: 2;
        }

        &::after {
            position: absolute;
            content: "";
            border-bottom: 2px solid #281981;
            width: 100%;
            top: 20px;
            left: 50%;
            z-index: 2;
        }

        &:last-child::after {
            content: none;
        }

        &:first-child::before {
            content: none;
        }

        &.completed {
            .step-counter {
                background: linear-gradient(270.9deg, var(--gradient-color-1) -2.92%, var(--gradient-color-2) 99.95%);
            }

            &::after {
                position: absolute;
                content: "";
                border-bottom: 2px solid #d53a9d;
                width: 100%;
                top: 20px;
                left: 50%;
                z-index: 3;
            }
        }

        &.active {
            > .step-counter {
                border-color: #d53a9d;
            }
        }

        > .step-counter {
            position: relative;
            z-index: 5;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background: #281981;
            margin-bottom: 6px;

            border: 1px solid transparent;
        }
    }
}

/**
 *      CUSTOM THEME
 */
.stepper-wrapper {
    margin-bottom: 40px;

    > .item {
        > .step-name {
            color: white;
        }
    }
}
