.circle-button {
    display: inline-block;
    border-radius: 50%;

    border: 1px solid transparent;
}

/**
 *      CUSTOM THEME
 */
.circle-button {
    height: 48px;
    width: 48px;
    background-color: #2e2380;

    > img {
        position: relative;
        top: 15%;
        left: 17%;
    }

    /*
     *      TODO: Gradient Colors
     */
    &:hover {
        border: 1px solid #d53a9d;

        cursor: pointer;
    }

    &.active {
        background-color: #d53a9d;
    }
}
